.navbar {
    position: fixed;
    width: 100%;
    background: #5138a1;
    top: 0px;
    left: 0px;
}

.navbar li {
    display: inline-block;
    width: 33.3%;
    color: white;
    text-align: center;
}

.navbar a {
    text-decoration: none;
    color: white;
}
.header {
    background-color: #2c323c;
    text-align: center;
    padding-top: 150px;
    padding-bottom: 150px;
    color: white;
    width: 100%;
}
img.btn {
    align-items: center;
    padding: 20px;
    width: 225px;
}
.footer {
    background-color:#5138a1;
    position: absolute;
    left: 0px;
    width: 100%;
    padding: 10px;
    color: white;
    /* margin-color: #6956af; */
}
